<script setup>
/* eslint-disable */
import {reactive, toRaw} from "vue";
let {dataSource} = defineProps(['dataSource'])
const formState = reactive({
  name: '',
  gender: '',
  birth: '',
});
const emits = defineEmits(['onSubmit']);
const onSubmit = () => {
  // console.log('submit!', toRaw(formState));
  let data = {
    key: '3',
    name: 'Edward King 3',
    birth: '2000-12-12',
    gender: 'female',
  };
  emits('dataSource-change-event',data)

};
</script>

<template>
  <a-form :model="formState" :label-col="labelCol" :wrapper-col="wrapperCol">
    <a-form-item label="user">
      <a-input v-model:value="formState.name" />
    </a-form-item>
    <a-form-item label="birth">
      <a-date-picker v-model:value="formState.birth" />
    </a-form-item>
    <a-form-item label="gender">
      <a-radio-group v-model:value="formState.gender">
        <a-radio value="1">male</a-radio>
        <a-radio value="2">female</a-radio>
      </a-radio-group>
    </a-form-item>
    <a-form-item :wrapper-col="{ span: 14, offset: 4 }" style="text-align: center">
      <a-button type="primary" @click="onSubmit">Create</a-button>
    </a-form-item>
  </a-form>
</template>
